import { addTask, taskarr } from './addTask.js';
// const dots = "./assets/dots.png";
const tasksList = document.querySelector('#tasksList');
const renderList = () => {
  tasksList.innerHTML = '';
  for (let i = 0; i < taskarr.length; i++) {
    // const element = array[i];
    const newTask = document.createElement('li');
    newTask.classList.add('newTask');
    newTask.innerHTML = `
      <input type="checkbox" id="checkB" ${taskarr[i].completed ? 'checked' : 'unchecked'} />
      <p class="newTasks" id="addItem" />${taskarr[i].description}</p>
      <img id="dotsImg" src="./assets/dots.png" alt="" />
      `;
    tasksList.appendChild(newTask);
  }
};

export { renderList };
